@import url(https://cdn.jsdelivr.net/npm/reset-css@5.0.1/reset.min.css);
* {
  box-sizing: border-box;
}
input {
  all: unset;
  box-sizing: border-box;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 14px;
  background-color: white;
}

button {
  background-color: white;
  color: black;
}

a {
  text-decoration: none;
  color: inherit;
}

form {
  width: 100%;
}

.nweet-input::-webkit-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input:-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input::-ms-input-placeholder {
  color: white;
  opacity: 0.9;
}

.nweet-input::placeholder {
  color: white;
  opacity: 0.9;
}

/*App.js*/
.Loading_message {
  width: 100vw;
  height: 100vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 120px;
}

.no-mobile {
  background-color: white;
  position: absolute;
  z-index: 99;
  height: 100%;
  width: 100%;
  color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 32px;
  top: 0;
  left: 0;
}
@media screen and (max-width: 800px) {
  .no-mobile {
    display: none;
  }
}

/* Auth nav-var */

.authContainer {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  align-items: center;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

/* nav-var */

.auth-nav-var {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 30px;
  padding: 30px;
}

.auth-nav-var-left {
  display: flex;
  justify-content: center;
  align-items: center;
}
.auth-nav-var-left span {
  margin-left: 10px;
}

.auth-nav-var-left-right {
  display: flex;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.3);
}
.auth-nav-var-left-right div:first-child {
  margin-right: 5px;
}

/* AuthForm (로그인계정)*/
.auth-container {
  text-align: center;
}
.authInput {
  text-align: start;
  max-width: 320px;
  width: 80%;
  padding: 10px;
  background-color: rgba(255, 255, 255, 1);
  margin-bottom: 5px;
  font-size: 14px;
  color: black;
}
.auth-save-account {
  margin-right: 170px;
}

.authSubmit {
  text-align: center;
  background: #04aaff;
  color: white;
  cursor: pointer;
  margin-top: 8px;
}

.authContainer input {
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.error_message {
  margin-top: 10px;
  color: tomato;
  text-align: center;
  font-weight: 500;
  font-size: 12px;
}

.authSwitch {
  color: black;
  cursor: pointer;
  margin-top: 10px;
  margin-bottom: 30px;
  display: block;
  font-size: 12px;
  text-decoration: underline;
}

.authForm-find {
  font-size: 12px;
  width: 70%;
  margin-bottom: 13px;
  display: flex;
  color: rgba(0, 0, 0, 0.5);
  justify-content: space-between;
  align-items: center;
}
.authForm-find span:first-child {
  color: black;
  opacity: 0.8;
}
.authBtns {
  display: flex;
  justify-content: space-between;
  width: 340px;
  margin-top: 10px;
}

.authBtn {
  margin: 0px 10px;
  cursor: pointer;
  border-radius: 3px;
  padding: 13px 0px;
  font-size: 12px;
  text-align: center;
  width: 150px;
  background: white;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.mobile-check {
  margin-top: 50px;
  color: rgba(0, 0, 0, 0.3);
  padding: 10px;
  font-size: 12px;
}

.auth-forgetPassword {
  border-bottom: 1px, solid black;
  margin-top: 50px;
  color: rgba(0, 0, 0, 0.3);
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 10px;
}

.navigation-ul {
  background-color: rgb(116, 202, 254);
  display: flex;
  padding: 10px;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  font-size: 10px;
}
.navigation-ul span {
  font-size: 15px;
  color: white;
}

#nav-fatwitter-icon {
  color: white;
  font-size: 25px;
}

.navigation-li:first-child {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.navigation-ul li:nth-child(3) img {
  width: 30px;
  height: 30px;
  border-radius: 100%;
}

.factoryForm {
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.nweet-ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -80px;
  width: 300px;
  height: 60px;
  background-color: rgb(15, 15, 15);
  padding: 20px;
}
.nweet-ul {
  color: white;
  font-size: 20px;
}

/*트윗 작성칸*/
.factoryInput__container {
  width: 300px;
  height: 120px;
  text-align: center;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.factoryInput__input {
  padding: 20px;
  display: flex;
  width: 100%;
  height: 60px;
}
.nweetfactory-imgAndPhoto {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  cursor: pointer;
  width: 100%;
}
.nweetfactory-imgAndPhoto input {
  background-color: rgb(0, 145, 234);
  color: white;
  width: 40px;
  height: 30px;
  text-align: center;
  border-radius: 5px;
  margin-top: -20px;
  margin-right: 8px;
  cursor: pointer;
}
.nweetfactory-imgAndPhoto span {
  cursor: pointer;
}

/*내가작성한 트윗목록 title*/
.nweetList {
  display: flex;
  justify-content: flex-start;
  margin-top: 40px;
  margin-bottom: -50px;
}
.nweetList i {
  margin-left: 5px;
}

/*작성한 트윗터목록*/
.nweet-creation-screen {
  border: 1px solid rgba(0, 0, 0, 0.2);
  position: relative;
  height: 160px;
  text-align: center;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}
.nweet-creation-screen_userAndProfile {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: -15px;
  left: -15px;
}
.nweet-creation-screen_profileImg {
  width: 40px;
  border-radius: 100%;
}
.nweet-creation-screen_userAndProfile h4 {
  margin-left: 5px;
  font-weight: 700;
  font-size: 15px;
}
.nweet-creation-screen_email {
  text-align: center;
  opacity: 0.2;
}
.nweet-creation-screen_text {
  height: 100px;
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nweet__actions {
  text-align: end;
  color: tomato;
  margin-right: 15px;
}
.nweet__actions span:first-child {
  margin-right: 10px;
  color: rgb(116, 202, 254);
}

.nweet-creation-screen_attachmentURL {
  width: 90px;
  height: 90px;
  position: absolute;
  right: 0;
  bottom: 20px;
}

/*사진추가하면 display*/
.factoryForm__attachment img {
  width: 80px;
  height: 70px;
  margin-bottom: 10px;
}

.factoryForm__attachment {
  text-align: start;
}

/*글 수정하는 화면*/
.nweetList {
  width: 100%;

  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}
.nweetEdit {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.nweetEdit-updateAndCancle {
  display: flex;

  justify-content: flex-end;
  align-items: center;
  margin: 10px 0px;
}
.nweetEdit-updateAndCancle input {
  padding: 3px;
  border-radius: 10px;
  margin-right: 10px;
  background-color: rgb(116, 202, 254);
  color: white;
}
.nweetEdit-cancle {
  padding: 3px;
  border-radius: 10px;
  background-color: tomato;
  color: white;
}

.profile-container {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: 20px;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  align-items: center;
  position: relative;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.profilePhoto {
  position: absolute;
  width: 90px;
  height: 90px;
  border-radius: 100%;
  left: 0px;
  top: 0px;
  z-index: 1;
}
#attach-file-icon {
  position: absolute;
  left: 70px;
  z-index: 1;
  top: 70px;
  font-size: 25px;
  border: 1px solid rgba(0, 0, 0, 0.8);
}
.profile-profile {
  text-align: start;
  width: 100px;
  margin-top: 60px;
  margin-left: -20px;
}
.profileModify {
  width: 100%;
  text-align: end;
  font-weight: 600;
}
.profile-center {
  position: absolute;
  top: 0px;
  right: 40px;
}

.profileIconBox {
  display: flex;
  font-size: 12px;
}
#attach-checkIcon {
  margin: 0px 5px;
}

.profileModify span {
  border: 1px solid rgb(116, 202, 254);
  border-radius: 10%;
  padding: 5px;
  color: rgb(116, 202, 254);
}

.profile-centerTileBox {
  width: 100%;
}
.profile-centerTile {
  width: 100%;
  margin-top: 20px;
  height: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
/*프로필 변경화면*/
.profile-mainscreen {
  margin-top: 40px;
  height: 200px;
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.2);
}

.profileForm {
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.profileInput {
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  width: 200px;
  height: 50px;
  margin-left: 15px;
}
.profileInput2 {
  width: 60px;
  height: 50px;
  border-radius: 10px;
  border: 1px solid rgba(0, 0, 0, 0.4);
  margin-left: 30px;
  margin-right: 10px;
}

.profileChangeBox {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  font-size: 15px;
  color: rgba(0, 0, 0, 0.4);
  margin-left: 8px;
}
#profileChangeBoxIcon {
  margin-left: 10px;
}

.logOutBox {
  height: 100px;
  display: flex;
  width: 100%;
  justify-content: flex-end;
  align-items: flex-end;
}
.logOut {
  margin-right: 1px;
  border-radius: 10px;
  padding: 5px;
  margin-bottom: 40px;
  color: white;
  background-color: rgb(116, 202, 254);
}
.profile-bottomTitle {
  font-size: 20px;
  opacity: 0.6;
  margin: 30px 0px;
}

.profile-mytweet {
  background-color: rgb(116, 202, 254);
  padding: 10px;
  color: white;
  border-radius: 18px;
}

